.Gallery_container{
    height: 400px;
    width: 100%;
    .Gallery_content{
        width: 100%;
        height: 320px;
        display: flex;
        justify-content: center;
        flex-direction: row;
        .content{
            width: 400px;
            color: white;
            text-align: center;
            img{
                width: 380px;
            }
        }
    }
}

.Gallery_background{
    position: absolute;
    left: 0;
    margin-top: -400px;
    height: 400px;
    width: 100%;
    background-color: #210D0F;
    z-index: -1;
}

#Gallery_anchor{
    position: absolute;
    left: 0;
    width: 100%;
    height: 0px;
    margin-top: -25vh;
}

@media all and (max-width: 1400px){
    .Gallery_container{
        .Gallery_content{
            .content{
                margin-top: 40px;
                width: 300px;
                img{
                    width: 270px;
                }
            }
        }
    }
}


@media all and (max-width: 700px){
    .Gallery_container{
        height: 720px;
        .Gallery_content{
            height: 620px;
            flex-direction: column;
            align-items: center;
            .content{
                margin-top: 10px;
            }
        }
    }

    .Gallery_background{
        position: absolute;
        left: 0;
        margin-top: -720px;
        height: 720px;
        width: 100%;
        background-color: #210D0F;
        z-index: -1;
    }
}
