.Contact_container{
    height: 240px;
    width: 100%;
    display: flex;
    justify-content: center;
    .Contact_content{
        width: 460px;
        height: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        .content{
            margin-top: 10px;
            width: 200px;
            color: white;
            text-align: left;
            div{
                display: flex;
                flex-direction: row;
                h2{
                    padding-left: 5px;
                }
            }
            #name{
	            font-family: GilroyEB;
                font-style: italic;
                color: #C7A460;
            }
        }
    }
    .background_container{
        position: absolute;
        width: 100%;
        height: 240px;
        max-width: 1920px;
        overflow: hidden;
        z-index: -1;
        img{
            position: absolute;
            top: -280%;
            width: 100%;
        }
    }
}

footer{
    margin-top: -40px;
    height: 40px;
    line-height: 40px;
    width: 100%;
    color: white;
    text-align: center;
}

#Contact_anchor{
    position: absolute;
    left: 0;
    width: 100%;
    height: 0px;
    margin-top: -25vh;
}

@media all and (max-width: 1400px){
    .Contact_container{
        .background_container{
            img{
                top: -50%;
            }
        }
    }
}

@media all and (max-width: 700px){
    .Contact_container{
        .Contact_content{
            justify-content: space-evenly;
        }
        .background_container{
            img{
                top: 0%;
            }
        }
    }
}