.header{
    top: 0;
    height: 180px;
    width: 100%;
    #top{
        margin-top: 320px;
        color: #210D0F;
    }
    #bottom{
        color: white;
    }
    .background_container{
        position: absolute;
        left: 50%;
        transform: translate(-50%);
        top: 110px;
        width: 100%;
        height: 500px;
        max-width: 1920px;
        overflow: hidden;
        z-index: -1;
        img{
            position: absolute;
            width: 100%;
        }
    }
}

#Home_anchor{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
}

@media all and (max-width: 1400px){
    .header {
        margin-bottom: 100px;
        #top{
            margin-top: 180px;
        }
        .background_container{
            img{
                left: 50%;
                transform: translate(-50%);
                height: 100%;
                width: auto;
            }
        }
    }
}

@media all and (max-width: 700px){
    .header {
        height: 0px;
        margin: 0;
        #top, #bottom{
            margin-top: 0px;
            font-size: 0px;
        }
        .background_container{
            height: 0px;
        }
    }
}
