.why_container{
    height: 400px;
    width: 100%;
    .why_content{
        width: 100%;
        height: 320px;
        display: flex;
        justify-content: center;
        flex-direction: row;
        .first_half, .second_half{
            display: flex;
            justify-content: center;
            flex-direction: row;
        }
        .content{
            padding: 0px 10px 0px 10px;
            margin-top: 70px;
            color: white;
            text-align: center;
        }
    }
}

.why_background{
    position: absolute;
    left: 0;
    margin-top: -400px;
    height: 400px;
    width: 100%;
    background-color: #210D0F;
    z-index: -1;
}

#Why_anchor{
    position: absolute;
    left: 0;
    width: 100%;
    height: 0px;
    margin-top: -25vh;
}

@media all and (max-width: 1400px){
    .why_container{
        .why_content{
            height: 280px;
            flex-direction: column;
            .content{
                margin-top: 0px;
            }
        }
    }
}