@font-face{
	src: url(./fonts/Gilroy-Light.otf);
	font-family: GilroyL;
}

@font-face{
	src: url(./fonts/Gilroy-ExtraBold.otf);
	font-family: GilroyEB;
}

html{
	scroll-behavior: smooth;
}

body{
	background-color: #C7A460;
	height: 100%;
	margin:0;
	padding:0;
	font-family: GilroyL;
}

h1{
	margin: 0;
    font-size: 13px;
}

h2{
	font-weight: 100;
	font-size: 15px;
}

h3{
    font-size: 30px;
	font-weight: 600;
	margin: 0;
}

h4{
	margin: 0;
    font-size: 40px;
	font-family: GilroyEB;
	font-weight: 800;
}

h5{
    font-size: 65px;
	font-family: GilroyEB;
	font-weight: 800;
	margin: 0;
	text-align: center;
}

@media all and (max-width: 700px){
	a{
		font-size: 2vw;
	}
    h1{
		font-size: 2vw;
	}
	
	h2{
		font-size: 3vw;
	}
	
	h3{
		font-size: 5vw;
	}
	
	h4{
		font-size: 6vw;
	}
	
	h5{
		font-size: 9vw;
	}
}