.Services_container{
    height: 400px;
    width: 100%;
    .Services_content{
        width: 100%;
        height: 320px;
        display: flex;
        justify-content: center;
        flex-direction: row;
        .first_half{
            display: flex;
            justify-content: center;
            flex-direction: row;
        }
        .content{
            padding: 0px 5px 0px 5px;
            margin-top: 10px;
            color: #210D0F;
            text-align: center;
            img{
                width: 150px;
            }
        }
    }
}

#Services_anchor{
    position: absolute;
    left: 0;
    width: 100%;
    height: 0px;
    margin-top: -25vh;
}


@media all and (max-width: 700px){
    .Services_container{
        height: 680px;
        .Services_content{
            flex-direction: column;
            .first_half{
                margin-top: 230px;
            }
            .second_half{
                padding: 0px 50px 0px 50px;
            }
        }
    }
}
