.about_container{
    height: 400px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    .text_container{
        width: 50%;
        margin-right: auto;
    }
    .image_container{
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        img{
            width: 100%;
            max-width: 663px;
            max-height: 400px;
        }
    }
    h2{
        color: white;
        text-align: justify;
        padding-right: 50px;
    }
    h3{
        margin: 0;
        color: #210D0F;
    }
    #top{
        margin-top: 60px;
    }
    #bottom{
        margin: 20px 0px 20px 0px;
    }
}

#About_anchor{
    position: absolute;
    left: 0;
    width: 100%;
    height: 0px;
    margin-top: -25vh;
}

@media all and (max-width: 700px){
    .about_container{
        height: 400px;
        flex-direction: column-reverse;
        .text_container{
            width: 100%;
            text-align: center;
            h2{
                padding: 0px 10px 0px 10px;
            }
            #top{
                margin-top: 0px;
            }
            #bottom{
                margin: 5px 0px 5px 0px;
            }
        }
        .image_container{
            position: relative;
            left: 25%;
            width: 50%;
            height: 200px;
            img{
                height: 200px;
                width: 100%;
                object-fit: cover;
                z-index: -2;
                content:url("../img/svg/logo.svg")
            }
        }
    }
}
